<style>
.detailtitle {
  line-height: 24px;
  border-left: 5px solid #2d8cf0;
  font-weight: bold;
  font-size: 16px;
  text-indent: 10px;
}
.detail_li .itemli {
  line-height: 40px;
  font-size: 14px;
}
.ivu-row {
  padding: 10px;
}

.detail_li .demo-badge-alone {
  height: 18px !important;
  line-height: 16px !important;
  padding: 0 4px !important;
  font-size: 12px !important;
}
.detail_tablebox {
  width: 100%;
  height: auto;
}
.setupload {
  /* width: 50px; */
  height: 30px;
  border: none;
  float: left;
  line-height: 1;
  margin-right: 5px;
}
.setupload .ivu-btn {
  padding: 2px 2px 2px;
  font-size: 12px;
}
.setupload .ivu-upload .ivu-upload-drag {
  border: 0px !important;
}
.successbtn {
  text-align: center;
}
.detail_title1 {
  display: inline-block;
  width: 70px;
  /* font-weight: bold; */
}
.detail_title2 {
  display: inline-block;
  width: 75px;
}
.detail_title3 {
  display: inline-block;
  width: 95px;
}
td >>> .ivu-table-cell {
  padding-left: 5px;
  padding-right: 5px;
}
th >>> .ivu-table-cell {
  padding-left: 5px;
  padding-right: 5px;
}
td /deep/ .ivu-table-cell {
  padding-left: 5px;
  padding-right: 5px;
}
th /deep/.ivu-table-cell {
  padding-left: 5px;
  padding-right: 5px;
}
.sethover {
  cursor: pointer;
}
.itembox >>> .ivu-select-input {
  font-size: 14px !important;
}
.itembox >>> .detail_tablebox >>> .ivu-input-wrapper {
  font-size: 14px !important;
}
.itembox span {
  font-size: 14px !important;
}
</style>
<template>
  <div>
    <div style="width:100%;font-size:12px;">
      <div class="itembox">
        <p class="detailtitle">基础信息</p>
        <!-- <p>{{getids}}</p> -->
        <div style="width:100%;min-height:200px;background:#f5f7f9;margin:15px 0">
          <Row>
            <Col style="width: 20%;float: left;">
              <ul class="detail_li detail1">
                <li class="itemli">
                  <span class="detail_title1">ID：</span>
                  <span>{{details.waybillinfo.ID}}</span>
                </li>
                <li class="itemli">
                  <span class="detail_title1">状态：</span>
                  <span>{{details.waybillinfo.ExcuteStatusDes}}</span>
                </li>
                <li class="itemli">
                  <span class="detail_title1">业务类型：</span>
                  <span>{{details.waybillinfo.SourceDes}}</span>
                </li>
                <li class="itemli" v-if="details.waybillinfo.Conditions!=undefined">
                  <Icon
                    type="md-alert"
                    v-if="Conditionstype"
                    style="font-size: 22px;color: #da2828;"
                  />
                  <Tag color="geekblue" v-if="details.waybillinfo.Conditions.AgencyCheck==true">代检查</Tag>
                  <Tag color="purple" v-if="details.waybillinfo.Conditions.AgencyPayment==true">代垫货款</Tag>
                  <Tag color="blue" v-if="details.waybillinfo.Conditions.AgencyReceive==true">代收货款</Tag>
                  <Tag color="cyan" v-if="details.waybillinfo.Conditions.ChangePackaging==true">代收货款</Tag>
                  <Tag color="green" v-if="details.waybillinfo.Conditions.LableServices==true">标签服务</Tag>
                  <Tag color="gold" v-if="details.waybillinfo.Conditions.PayForFreight==true">垫付运费</Tag>
                  <Tag color="orange" v-if="details.waybillinfo.Conditions.Repackaging==true">重新包装</Tag>
                  <Tag color="volcano" v-if="details.waybillinfo.Conditions.UnBoxed==true">拆箱验货</Tag>
                  <Tag color="red" v-if="details.waybillinfo.Conditions.VacuumPackaging==true">真空包装</Tag>
                  <Tag
                    color="magenta"
                    v-if="details.waybillinfo.Conditions.WaterproofPackaging==true"
                  >防水包装</Tag>
                  <Tag
                    color="lime"
                    v-if="details.details.waybillinfo.Conditions.QualityInspection==true"
                  >质检</Tag>
                  <Tag
                    color="yellow"
                    v-if="details.details.waybillinfo.Conditions.Unboxing==true"
                  >拆包装</Tag>
                </li>
              </ul>
            </Col>
            <Col style="width: 26%;float: left;">
              <ul class="detail_li">
                <li class="itemli">
                  <span class="detail_title2">制单时间：</span>
                  <span>{{details.waybillinfo.CreateDate|showDate}}</span>
                </li>
                <li class="itemli">
                  <span class="detail_title2">到货时间：</span>
                  <span>{{details.waybillinfo.CreateDate|showDate}}</span>
                </li>
                <li class="itemli">
                  <span class="detail_title2">供应商：</span>
                  <span>{{details.waybillinfo.Supplier}}</span>
                  <Tooltip content="一级" placement="right">
                    <img style="vertical-align: middle" src="../../assets/img/vip_1.png" alt />
                  </Tooltip>
                  <br />
                </li>
                <li class="itemli">
                  <span class="detail_title2">到货方式：</span>
                  <span>{{details.waybillinfo.TypeDes}}</span>
                  <!-- <a href="">历史到货
                         <Badge :count="10"></Badge>
                  </a>-->
                  <Badge
                    :count="historydetail.waybillLIst.length"
                    :offset="[9,-12]"
                    class-name="demo-badge-alone"
                    v-if="historydetail.waybillLIst.length>0"
                  >
                    <!-- <a @click="showhistory">历史到货</a> -->
                    <Dropdown @on-click="showhistory">
                      <a href="javascript:void(0)">历史到货</a>
                      <DropdownMenu slot="list">
                        <DropdownItem
                          v-for="(item,index) in historydetail.waybillLIst"
                          :name="item"
                          :key="index"
                        >
                          <span>{{item}}</span>
                        </DropdownItem>
                      </DropdownMenu>
                    </Dropdown>
                  </Badge>
                </li>
                <li class="itemli">
                  <span class="detail_title2">订单号：</span>
                  <span
                    style="line-height: 20px;width: 70%;display: inline-block;"
                  >{{details.waybillinfo.OrderID}}</span>
                </li>
              </ul>
            </Col>
            <Col style="width: 31%;float: left;">
              <ul class="detail_li">
                <li class="itemli">
                  <span class="detail_title3">入仓号：</span>
                  <span>{{details.waybillinfo.EnterCode}}</span>
                  <em>({{details.waybillinfo.ClientName}})</em>
                </li>
                <li class="itemli">
                  <span class="detail_title3">运单号(本次)：</span>
                  <span>
                    <Input style="width:60%" v-model="details.waybillinfo.Code" />
                  </span>
                </li>
                <li class="itemli">
                  <span class="detail_title3">承运商(本次):</span>
                  <span>
                    <Select
                      v-model="details.waybillinfo.CarrierID"
                      style="width:60%"
                      filterable
                      allow-create
                      @on-create="handleCreate1"
                      @on-change="changeacrrier"
                      :label-in-value="true"
                    >
                      <Option
                        v-for="(item,index) in CarrierList"
                        :value="item.ID"
                        :key="index"
                      >{{ item.Name }}</Option>
                    </Select>
                  </span>
                </li>

                <li class="itemli">
                  <span class="detail_title3">司&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;机:</span>
                  <span>
                    <Select
                      v-model="details.waybillinfo.Driver"
                      style="width:60%"
                      :label-in-value="true"
                      transfer
                      filterable
                      allow-create
                      @on-create="handleCreate2"
                      @on-change="changedrivers"
                    >
                      <Option
                        v-for="(item,index) in DriversArr"
                        :value="item.ID"
                        :key="index"
                      >{{ item.Name }}</Option>
                    </Select>
                  </span>
                </li>
                <li class="itemli">
                  <span class="detail_title3">车&nbsp;&nbsp;牌&nbsp;&nbsp;号:</span>
                  <span>
                    <Select
                      transfer
                      v-model="details.waybillinfo.CarNumber1"
                      style="width:60%"
                      :label-in-value="true"
                      @on-create="handleCreate3"
                      @on-change="changedrivers"
                    >
                      <Option
                        v-for="item in CarArr"
                        :value="item.ID"
                        :key="item.ID"
                      >{{ item.CarNumber1 }}&nbsp;&nbsp;&nbsp;&nbsp;{{ item.CarNumber2 }}</Option>
                    </Select>
                  </span>
                </li>
                <li class="itemli">
                  <span class="detail_title3">输送地：</span>
                  <span>
                    <!-- <Input style="width:60%" v-model="details.Conveyorsite" /> -->
                    <span
                      v-if="details.waybillinfo.ConsignorPlace!=0"
                    >{{details.waybillinfo.ConsignorPlaceText}}</span>
                    <span v-else style="color:red;">暂无输送地</span>
                    <Icon
                      class="sethover"
                      @click="clickClient(details.waybillinfo.ConsignorPlace,'ClientCode')"
                      type="md-create"
                    />
                  </span>
                </li>
              </ul>
            </Col>
            <Col style="width: 23%;float: left;">
              <ul class="detail_li" style="margin-left:20px;">
                <li class="itemli">
                  <!-- <img-test ref="allimg" :type="1"></img-test> -->

                  <div class="setupload">
                    <Button
                      type="primary"
                      size="small"
                      icon="ios-cloud-upload"
                      @click="SeletUpload(details.waybillinfo.WaybillID)"
                    >传照</Button>
                  </div>
                  <div class="setupload">
                    <!-- <Button type="primary" icon="ios-search" @click="photoing('waybill')">拍照</Button> -->
                    <Button
                      type="primary"
                      size="small"
                      icon="md-reverse-camera"
                      @click="fromphotos('Waybill')"
                    >拍照</Button>
                  </div>
                </li>
                <li style="clear: both;">
                  <div v-for="(item,index) in details.waybillinfo.Files" v-if="index<10">
                    <span>{{item.CustomName}}</span>
                    <Icon type="ios-trash-outline" @click.native="handleRemove(item)"></Icon>
                  </div>
                </li>
                <li
                  style="position: absolute; top: 147px; right: 0;"
                  v-if="details.waybillinfo.Type==1"
                >
                  <Button type="primary" @click="TakeGoods">我去提货</Button>
                </li>
              </ul>
            </Col>
          </Row>
          <!-- <Modal v-model="showphoto2" title="拍照并上传" width="1006px">
            <photo-graph ref="photograph" v-on:changupload="changupload($event,fathertype)"></photo-graph>
            <div slot="footer">
              <Button type="text" ref="clostbtn" @click="closephoto">关闭</Button>
              <Button type="primary" ref="okbtn" @click="upload_btn">确定</Button>
            </div>
          </Modal> -->
        </div>
      </div>
      <div class="itembox">
        <!-- 未通知产品信息录入  开始-->
        <Modal
          v-model="abnormal_enter"
          title="无通知产品录入"
          width="90"
          ref="modal"
          @on-ok="ok_abnormal"
          @on-cancel="cancel"
          @on-visible-change="changeabnormal_enter"
        >
          <div>
            <div>
              <p class="detailtitle">
                无通知产品录入
                <span style="margin-left: 15px;color:red;">注意：仅能录入该运单下与通知型号不匹配的产品信息</span>
              </p>
              <div style="padding:15px 0px;">
                <Noting-Enter :key="timer" ref="NotingEntername" :Storagesarr="Storages" :Storehouselistarr="Storehouselist" :Boxingarr='boxingarr' :status='1'></Noting-Enter>
              </div>
            </div>
          </div>
        </Modal>

        <!-- 未通知产品信息录入 结束 -->
        <p class="detailtitle">通知产品清单</p>
        <div style="margin:15px 0">
          <ButtonGroup style="width:28%">
            <Input
              v-model="searchkey"
              placeholder="请输入品牌或型号"
              clearable
              @on-clear="search_pro"
              style="width:80%;float:left;position: relative;left: 3px"
            />
            <Button style="float:left" @click="search_pro" type="primary">筛选</Button>
          </ButtonGroup>
          <!-- <Button type="primary" @click="detailelist" :disabled='true'>清单打印</Button>
          <Button type="primary" @click="Labelprint" :disabled='true'>标签打印</Button> -->
          <Button type="primary" @click="SetStorehouse" :disabled="isdisabled==true?true:false">一键入库</Button>
          <Button type="primary" @click="showBudget('meet','in',details.waybillinfo.Source)">收入</Button>
          <Button type="primary" @click="showBudget('meet','out',details.waybillinfo.Source)">支出</Button>
          <Button type="primary" @click="showBudget('oplog')" :disabled='true'>操作日志</Button>
          <Button type="primary" @click="abnormal_enter=true" :disabled="isdisabled==true?true:false">无通知产品录入</Button>
          <Button type="primary" @click="testbtn('12323123123')">代转运测试</Button> 
          <div style="text-align: right;" v-if="details.waybillinfo.OStatus!=200">
            <Button type="primary" shape="circle" icon="md-checkmark" @click="finish_btn" :disabled="isdisabled==true?true:false">入库完成</Button>
            <Button type="warning" shape="circle" icon="ios-alert-outline" @click="Abnormal" :disabled="isdisabled==true?true:false">到货异常</Button>
          </div>
        </div>
        <div>
          <div class="detail_tablebox">
            <Table
              ref="selection"
              :loading="loading"
              :columns="details.columns1"
              :data="details.detailitem"
              size="large"
              @on-selection-change="handleSelectRow"
            >
              <template slot-scope="{ row, index }" slot="indexs">{{index+1}}</template>
              <template slot-scope="{ row, index }" slot="Arrival">
                <span v-if="row.iscx!=false">{{row.Quantity}}</span>
              </template>
              <template slot-scope="{ row, index }" slot="Already">
                <span v-if="row.iscx!=false">{{row.ArrivedQuantity}}</span>
              </template>
              <template slot-scope="{ row, index }" slot="Surplus">
                  <P v-if="row.iscx!=false">
                    <span v-if="Number(row.LeftQuantity)<=0">0</span> 
                    <span v-else>{{row.LeftQuantity}}</span> 
                 </P>
              </template>
              <template slot-scope="{ row, index }" slot="OriginDes">
                <span>{{row.origintext}}</span>
                <Icon class="sethover" v-if="row._disabled!=true" @click="clickClient(row.origin,row)" type="md-create" />
              </template>
              <template slot-scope="{ row, index }" slot="ShelveID">
                <Select
                  :disabled="row._disabled==true?true:false"
                  size="default"
                  v-model="row.ShelveID"
                  filterable
                  :transfer="true"
                  :label-in-value="true"
                  @on-change="changConveyingplace($event,row)"
                >
                  <Option
                    v-for="(item,index) in Storehouselist"
                    :value="item.ShelveID"
                    :label="item.ShelveID"
                    :key="index"
                  >
                    <span>{{item.ShelveID}}</span>
                    <span style="float:right;color:#ccc">{{item.TotalPackage}}</span>
                  </Option>
                </Select>
              </template>
              <template slot-scope="{ row, index }" slot="imglist">
                <p v-for="(item,index) in row.Files">
                  <span>{{item.CustomName}}</span>
                  <Icon
                    type="ios-trash-outline"
                    :ref="row.ID"
                    @click.native="handleRemovelist(row,index)"
                  ></Icon>
                </p>
                <!-- <Input v-model="row.typeimg" /> -->
              </template>
              <template slot-scope="{ row, index }" slot="operation">
                <div class="setupload">
                  <Button
                    :disabled="row._disabled==true?true:false"
                    type="primary"
                    size="small"
                    icon="ios-cloud-upload"
                    @click="SeletUpload(row.ID)"
                  >传照</Button>
                </div>
                <div class="setupload">
                  <Button
                    :disabled="row._disabled==true?true:false"
                    type="primary"
                    size="small"
                    icon="md-reverse-camera"
                    @click="fromphotos(row)"
                  >拍照</Button>
                </div>
                <div class="setupload" v-if="row.iscx!=false">
                  <Button
                    :disabled="row._disabled==true?true:false"
                    type="primary"
                    size="small"
                    icon="md-checkmark"
                    @click="chaixiang(index,row)"
                  >拆项</Button>
                </div>
                <div class="setupload" v-if="row.iscx==false">
                  <Button
                    :disabled="row._disabled==true?true:false"
                    v-if="row.iscx==false||row.isenter==true"
                    type="primary"
                    size="small"
                    icon="ios-trash-outline"
                    @click="removechaixiang(index,row)"
                  >删除</Button>
                </div>
              </template>
            </Table>
            <div style="padding:10px;">
              <!-- <Page style="float:right" :total="details.total" :page-size='details.pageSize' @on-change='detailspage' /> -->
            </div>
            <div class="successbtn" v-if="details.waybillinfo.OStatus!=200">
              <Button type="primary" icon="md-checkmark" @click="finish_btn" :disabled="isdisabled==true?true:false">入库完成</Button>
              <Button type="warning" icon="ios-alert-outline" @click="Abnormal" :disabled="isdisabled==true?true:false">到货异常</Button>
            </div>
          </div>
        </div>
      </div>
      <div v-if="WarehousingMsg==true">
        <Modal
          v-model="WarehousingMsg"
          title="确定入库"
          @on-ok="ok_Warehousing(115)"
          @on-cancel="cancel_Warehousing"
        >
          <div v-if="Nomatching.length!=0">
            <!-- <span v-for="(item,index) in Nomatching" :key="index">{{item}}与通知型号不都，是否入库</span> -->
            <h2>以下型号数量与通知数量不符，是否继续进行入库操作</h2>
            <ul>
              <li v-for="(item,index) in Nomatching" :key="index">
                <h2>{{item}}</h2>
              </li>
            </ul>
          </div>
          <div v-else>
            <span>是否全部入库</span>
          </div>
        </Modal>
      </div>
      <!-- 一键入库 开始-->
      <Modal v-model="sethousbox" title="一键入库" @on-cancel="cancel">
        <div slot="close">
          <Icon
            type="md-close-circle"
            color="rgb(33, 28, 28)"
            @click="closeerror"
            style="font-size:18px;"
          />
        </div>
        <span style="line-height:26px;">选择库位</span>
        <Select v-model="housenumber" filterable v-if="Storehouselist.length>0">
          <Option
            v-for="(item,index) in Storehouselist"
            :value="item.ShelveID"
            :label="item.ShelveID"
            :key="index"
          >
            <span>{{item.ShelveID}}</span>
            <span style="float:right;color:#ccc">{{item.TotalPackage}}</span>
          </Option>
        </Select>
        <div slot="footer">
          <Button>取消</Button>
          <Button type="primary" @click="changehouse">确定</Button>
        </div>
      </Modal>
      <!-- 一键入库 结束-->

      <!-- 异常到货 开始-->
      <Modal v-model="isAbnormal" title="到货异常" @on-ok="ok_Warehousing(130)" @on-cancel="closeerror">
        <div slot="close">
          <Icon
            type="md-close-circle"
            color="rgb(33, 28, 28)"
            @click="closeerror"
            style="font-size:18px;"
          />
        </div>
        <span style="line-height:26px;">异常原因</span>
        <Input v-model="Summary" type="textarea" :rows="2" placeholder="备注" />
      </Modal>
      <!-- 异常到货 结束-->
    </div>
    <!-- 历史到货 开始 -->
    <Drawer :width="70" v-model="historydata">
      <Historys-dom :key="historydetail.times" ref="Historygoods"></Historys-dom>
    </Drawer>
    <!-- 历史到货 结束 -->

    <!-- 收支明细 开始 -->
    <Modal
      v-model="Budgetdetail"
      width="55%"
      :closable="false"
      :mask-closable="false"
      :footer-hide="true"
    >
      <div style="clear: both; overflow: hidden;font-size:16px;padding-bottom:10px">
        <Icon type="ios-close-circle" style="float:right" @click="closeBudget" />
      </div>
      <div>
        <router-view></router-view>
      </div>
    </Modal>
    <!-- 收支明细结束 -->

    <!-- 输送地列表与更改 开始-->
    <Modal v-model="setClientCode" title="选地地区" @on-cancel="cancel">
      <Select v-model="ClientCode" filterable :label-in-value="true" @on-change="changClientCode">
        <Option
          v-for="(item,index) in Conveyingplace2"
          :value="item.CorPlace"
          :key="item.ID"
        >{{ item.Text }}</Option>
      </Select>
      <div slot="footer">
        <Button @click="cancel">取消</Button>
        <Button type="primary" @click="primaryClientCode">确定</Button>
      </div>
    </Modal>
    <!-- 输送地列表与更改 结束-->
  </div>
</template>
<script>
// import imgtest from "@/Pages/Common/imgtes";
// import Photograph from "@/Pages/Common/Photograph";
import Historys from "@/Pages/Cgenter/Historygoods";
import NotingEnter from "@/Pages/Cgenter/NotingEnter";
// import Vue from 'vue'
import {
  Noticedetail,
  sortingupload,
  getWayParter,
  Carriers,
  History
} from "../../api";
import {
  CgDetail,
  cgenter,
  GetSortingID,
  historyList,
  GetDriversCars,
  search_detail,
  TakeGoods,
  GetUsableShelves
} from "../../api/CgApi"; //引入api 的接口
import {
  TemplatePrint,
  GetPrinterDictionary,
  FilePrint,
  FormPhoto,
  SeletUploadFile
} from "@/js/browser.js";
let Base64 = require("js-base64").Base64;
let product_url = require("../../../static/pubilc.dev");
import Vue from "vue";
import moment from "moment";
import qs from "qs";
let lodash = require("lodash");

// var getuplode=""

export default {
  name: "RoutineEnter",
  components: {
    // "img-test": imgtest,
    // "photo-graph": Photograph,
    "Historys-dom": Historys,
    "Noting-Enter": NotingEnter
  },
  props: {
    fatherMethod: {
      type: Function,
      default: null
    }
  },
  data() {
    return {
      boxingarr:[],//箱号
      isdisabled:false,
      timer: "", //当前时间，用于加载子组件
      abnormal_enter: false,
      SortingID: "",
      routername: "",
      Conditionstype: true,
      printurl: product_url.pfwms,
      loading: true, //loading效果
      printlist: [], //打印列表
      Conveyingplace: [], //输送地列表
      Conveyingplace2: [],
      chengevalue: {
        inputval: "",
        value: "",
        type: ""
      },
      setClientCode: false, //显示输送地选择模态框
      ClientCode: "", //默认输送地
      model11: "",
      sethousbox: false, //一键入库弹出框
      housenumber: "", //选择的库位号
      Storehouselist: [], //库位号列表
