        } else {
          this.isAbnormal = true;
        }
      }
    },
    closeerror() {
      //异常到货关闭
      this.isAbnormal = false;
      this.Summary = ""; //备注
    },
    finish_btn() {
      if (this.SelectRow.length <= 0) {
        this.$Message.error("请选择要操作的产品项");
      } else {
        console.log(this.SelectRow);
        bbq: for (var i = 0, len = this.SelectRow.length; i < len; i++) {
          if (
            this.SelectRow[i].LeftQuantity == 0 ||
            this.SelectRow[i].LeftQuantity == ""
          ) {
            this.$Message.error("无通知产品请按照异常到货处理");
            this.WarehousingMsg = false;
            break;
          } else if (this.SelectRow[i].originID == "") {
            this.$Message.error("请选择原产地");
            this.WarehousingMsg = false;
            break;
          } else {
            if (this.SelectRow[i].CurrentQuantity == "") {
              this.$Message.error("数量为必填，请输入本次数量");
              this.WarehousingMsg = false;
              break;
            } else {
              function sum(arr) {
                return eval(arr.join("+"));
              }
              var arr = this.SelectRow;
              var Nomatching = [];
              var map = {},
                dest = [];
              for (var i = 0; i < arr.length; i++) {
                var ai = arr[i];
                if (!map[ai.NoticeID]) {
                  dest.push({
                    name: ai.Product.PartNumber,
                    Quantity: ai.LeftQuantity,
                    NoticeID: ai.NoticeID,
                    data: [Number(ai.CurrentQuantity)]
                  });
                  map[ai.NoticeID] = ai.NoticeID;
                } else {
                  for (var j = 0; j < dest.length; j++) {
                    var dj = dest[j];
                    if (dj.NoticeID == ai.NoticeID) {
                      dj.data.push(Number(ai.CurrentQuantity));
                      break;
                    }
                  }
                }
              }
              var that = this;
              for (var i = 0; i < dest.length; i++) {
                var item = dest[i];
                var total = sum(item.data);
                console.log(total);
                if (item.Quantity == "") {
                  this.$Message.error("无通知产品请按照异常到货处理");
                  that.WarehousingMsg = false;
                  break bbq;
                } else if (item.Quantity < total) {
                  this.$Message.error("本次到货数量不能大于剩余数量");
                  that.WarehousingMsg = false;
                  break bbq;
                } else {
                  if (item.Quantity == 0) {
                    this.$Message.error("剩余数量为0，不需要提交");
                    that.WarehousingMsg = false;
                    break bbq;
                  } else {
                    if (item.Quantity > total) {
                      Nomatching.push(item.name);
                      that.Nomatching = Nomatching;
                      that.WarehousingMsg = true;
                    } else if (item.Quantity == total) {
                      that.WarehousingMsg = true;
                    }
                  }
                }
              }
              console.log(dest);
            }
          }
        }
      }
    },
    setWarehousing(data) {
      cgenter(data).then(res => {
        console.log(res);
        if (res.Success == true) {
          if(this.details.waybillinfo.Source==40){
             this.$Message.success("入库完成，正在跳转至出库分拣页面");
              var that = this;
              setTimeout(function() {
