        if (this.routername == "CgUntreated") {
          namemeet = "/CgUntreated/Income";
        } else {
          namemeet = "/CgProcessed/Income";
        }
        this.$router.push({
          path: namemeet,
          query: {
            webillID: this.details.waybillinfo.ID,
            OrderID:this.details.waybillinfo.OrderID,
            type: Budget,
            otype:"in",
            conduct:Source
          }
